.chat {
    border: 1px solid var(--color-grey-dark);
    border-radius: 5px;
}

.chatDialog {
    padding: 20px;
    max-height: 440px;
    overflow-y: auto;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.messageDate {
    font-family: 'Inter', sans-serif;
    font-size: 12px;
    text-align: center;
    font-weight: 500;
    margin-bottom: 8px;
}

.messageWrapper {
    display: flex;

}

.messageWrapper:nth-child(even) {
    justify-content: flex-end;
}

.message {
    width: 86%;
}

.messageInner {
    background: #E5EFF1;
    border: 1px solid #E5EFF1;
    border-radius: 5px;
    font-size: 14px;
    padding: 16px;
    color: #000000;
    line-height: 18px;
}

.chatInputWrapper {
    display: flex;
    border-top: 1px solid var(--color-grey-dark);
    background: #E5EFF1;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.chatInput {
    outline: none;
    border: none;
    border-right: 1px solid var(--color-grey-dark);
    border-bottom-left-radius: 5px;
    padding: 14px;
    background: #E5EFF1;
    width: 75%;
    font-size: 14px;
}

.chatInputBtn {
    width: 25%;
    outline: none;
    border: none;
    background: #E5EFF1;
    border-bottom-right-radius: 5px;
    cursor: pointer;
}

.chatInputBtn svg {
    width: 40px;
}

.disabled {
    cursor: initial;
}

.chatInputBtn:not(.disabled):hover {
    background: #c4e9ef;
}

.chatInputBtn span {
    display: none;
}

@media screen and (min-width: 767px) {
    .chatInputBtn, .chatInput {
        font-size: 18px;
    }

    .chatInputBtn svg {
        display: none;
    }

    .chatInputBtn span {
        display: initial;
    }
}